}{margin-right: 5px *$i;}	}	/*-- Padding Top --*/	@for $i from 1 through 40 {		.pt-#{5 * $i}{padding-top: 5px *$i;}	}	.pt-260{		padding-top: 260px;	}	/*-- Padding Bottom --*/	@for $i from 1 through 40 {		.pb-#{5 * $i}{padding-bottom: 5px *$i;}	}	/*-- Padding Left --*/	@for $i from 1 through 40 {		.pl-#{5 * $i}{padding-left: 5px *$i;}	}	/*-- Padding Right --*/	@for $i from 1 through 40 {		.pr-#{5 * $i}{padding-right: 5px *$i;}	}// others common css here :)/*-- Some Animated CSS -- *//* bounce-animate */.bounce-animate{	animation-name: float-bob;	animation-duration: 2s;	animation-iteration-count: infinite;	/* animation-timing-function: linear; */	-moz-animation-name: float-bob;	-moz-animation-duration: 2s;	-moz-animation-iteration-count: infinite;	-moz-animation-timing-function: linear;	-ms-animation-name: float-bob;	-ms-animation-duration: 2s;	-ms-animation-iteration-count: infinite;	-ms-animation-timing-function: linear;	-o-animation-name: float-bob;	-o-animation-duration: 2s;	-o-animation-iteration-count: infinite;	-o-animation-timing-function: linear;	}  @-webkit-keyframes float-bob {	0% {	  -webkit-transform: translateY(-20px);	  transform: translateY(-20px); }	50% {	  -webkit-transform: translateY(-10px);	  transform: translateY(-10px); }	100% {	  -webkit-transform: translateY(-20px);	  transform: translateY(-20px); } }	  /* heartbeat */	.heartbeat {		animation: heartbeat 1s infinite alternate;	}	@-webkit-keyframes heartbeat {		to {			-webkit-transform: scale(1.03);			transform: scale(1.03);		}	}/* rotateme */	.rotateme {		-webkit-animation-name: rotateme;		animation-name: rotateme;		-webkit-animation-duration: 30s;		animation-duration: 30s;		-webkit-animation-iteration-count: infinite;		animation-iteration-count: infinite;		-webkit-animation-timing-function: linear;		animation-timing-function: linear;	}	@keyframes rotateme {		from {			-webkit-transform: rotate(0deg);			transform: rotate(0deg);		}		to {			-webkit-transform: rotate(360deg);			transform: rotate(360deg);		}	}	@-webkit-keyframes rotateme {		from {			-webkit-transform: rotate(0deg);		}		to {			-webkit-transform: rotate(360deg);		}	}/* 20. preloader */	.preloader {		background-color: #f7f7f7;		width: 100%;		height: 100%;		position: fixed;		top: 0;		left: 0;		right: 0;		bottom: 0;		z-index: 999999;		-webkit-transition: .6s;		-o-transition: .6s;		transition: .6s;		margin: 0 auto;	}	.preloader .preloader-circle {		width: 100px;		height: 100px;		position: relative;		border-style: solid;		border-width: 1px;		// border-top-color:$theme-color;		border-top-color:$theme-color;		border-bottom-color: transparent;		border-left-color: transparent;		border-right-color: transparent;		z-index: 10;		border-radius: 50%;		-webkit-box-shadow: 0 1px 5px 0 rgba(35, 181, 185, 0.15);		box-shadow: 0 1px 5px 0 rgba(35, 181, 185, 0.15);		background-color: #fff;		-webkit-animation: zoom 2000ms infinite ease;		animation: zoom 2000ms infinite ease;		-webkit-transition: .6s;		-o-transition: .6s;		transition: .6s;	}	.preloader .preloader-circle2 {		border-top-color: #0078ff;	}	.preloader .preloader-img {		position: absolute;		top: 50%;		z-index: 200;		left: 0;		right: 0;		margin: 0 auto;		text-align: center;		display: inline-block;		-webkit-transform: translateY(-50%);		-ms-transform: translateY(-50%);		transform: translateY(-50%);		padding-top: 6px;		-webkit-transition: .6s;		-o-transition: .6s;		transition: .6s;	}	.preloader .preloader-img img {		max-width: 55px;	}	.preloader .pere-text strong{		font-weight: 800;		color:#dca73a ;		text-transform: uppercase;	}	@-webkit-keyframes zoom {		0% {			-webkit-transform: rotate(0deg);			transform: rotate(0deg);			-webkit-transition: .6s;			-o-transition: .6s;			transition: .6s;		}		100% {			-webkit-transform: rotate(360deg);			transform: rotate(360deg);			-webkit-transition: .6s;			-o-transition: .6s;			transition: .6s;		}	}	@keyframes zoom {		0% {			-webkit-transform: rotate(0deg);			transform: rotate(0deg);			-webkit-transition: .6s;			-o-transition